// Module
___CSS_LOADER_EXPORT___.push([module.id, `.graph-panel {
    padding: 20px;
    flex: 1;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
    flex-direction: column;
}
.graph-panel h3 {
    margin-top: 0;
    margin-bottom: 20px;
    color: #333;
    border-bottom: 2px solid #eee;
    padding-bottom: 10px;
    font-size: 1.4em;
}

.graph-row {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 5px;
}
.graph-row label {
    width: 350px;
    font-size: 1.1em;
    font-weight: 600;
    margin-bottom: 8px;
    color: #34495e;
}
.graph-row input[type="range"] {
    flex: 1;
    height: 8px;
    cursor: pointer;
    background: #bdc3c7;
    border-radius: 4px;
}
.graph-row input[type="number"] {
    width: 80px;
    height: 50px;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
    text-align: center;
    box-sizing: border-box
}

.experiment-button {
    width: 100%;
    padding: 12px;
    margin-top: 20px;
    border: none;
    border-radius: 8px;
    background-color: #e74c3c;
    color: white;
    font-size: 1.1em;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s, box-shadow 0.1s;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}
.experiment-button:hover {
    background-color: #c0392b;
    box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
}
.experiment-button:disabled {
    background-color: #95a5a6;
    cursor: not-allowed;
    box-shadow: none;
}

/* -------------------------------------------------- */
/*  */
/* -------------------------------------------------- */

@media (max-width: 1024px) {
    .experiment-main-layout,
    .graph-controls {
        flex-direction: column;
        margin: 20px auto;
    }

    .experiment-main-layout > *,
    .graph-controls > * {
        flex: 1 1 100%;
    }
