/* 1.  */
/* ========================================================= */

.graph-panel1 {
  padding: 20px;
  max-width: 700px;
  flex: 1;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
  min-width: 600px;
}

.graph-panel h3 {
  margin-top: 0;
  margin-bottom: 20px;
  color: #333;
  border-bottom: 2px solid #ddd;
  padding-bottom: 10px;
  font-size: 1.2em;
}

/* ========================================================= */
/* 2. Row  () */
/* ========================================================= */

.row-container {
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 12px;
}

.row-label {
  max-width: 70%;
  font-weight: 500;
  color: #333;
  text-align: left;
}

.row-controls {
  display: flex;
  align-items: center;
  gap: 8px;
  flex-grow: 1;
}

/* ========================================================= */
/* 3.  */
/* ========================================================= */

.input-compact {
  width: 60px;
  padding: 5px 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  text-align: right;
  font-size: 14px;
}

input[type="range"] {
  flex-grow: 1;
  margin: 10px 0;
  width: auto;

  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;

  height: 8px;
  background: #ddd;
  border-radius: 4px;
  cursor: pointer;
}

input:disabled,
input[readonly] {
  background-color: #eee;
  color: #aaa;
  cursor: not-allowed;
}

/* ========================================================= */
/* 4.  */
/* ========================================================= */
.text-alert {
  border-color: #e74c3c;
  box-shadow: 0 0 5px rgba(231, 76, 60, 0.5);
  background-color: #fbecec;
}

/* ========================================================= */
/* 5.  */
/* ========================================================= */
.btn-group {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 20px;
  padding-top: 10px;
  border-top: 1px dashed #eee;
}

.btn {
  padding: 8px 15px;
  border: 1px solid #3498db;
  border-radius: 4px;
  background-color: #3498db;
  color: white;
  cursor: pointer;
  transition: background-color 0.2s;
  font-size: 14px;
}

.btn:hover:not(.active) {
  background-color: #2980b9;
}

/*  */
.btn.active {
  background-color: #2ecc71;
  border-color: #2ecc71;
  font-weight: bold;
}

.btn.active:hover {
  background-color: #27ae60;
