___CSS_LOADER_EXPORT___.push([module.id, `/* ========================================================= */
/* 1.  (CurrentHumidityDisplay) */
/* ========================================================= */

.graph-panel2 {
  padding: 20px;
  max-width: 700px;
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
  min-width: 300px;
}
.graph-panel2 h3 {
  margin-top: 0;
  margin-bottom: 20px;
  color: #333;
  border-bottom: 2px solid #ddd;
  padding-bottom: 10px;
  font-size: 1.2em;
}

/* ========================================================= */
/* 2. ReadRow  */
/* ========================================================= */

.read-row-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 12px;
}

.read-row-label {
  max-width: 70%;
  font-weight: 500;
  color: #333;
  text-align: left;
}

.read-row-value-group {
  display: flex;
  align-items: center;
  gap: 4px;
  flex-shrink: 0;
}
.read-row-value-group span {
    font-size: 14px;
    color: #555;
    line-height: 1.5;
}

/* ========================================================= */
/* 3.  */
/* ========================================================= */

.read-row-input-compact {
  width: 75px;
  padding: 6px 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  text-align: right;
  font-size: 15px;
  background-color: rgb(230, 230, 230);
  color: #333;
  cursor: default;
